.department {
    width: 100%;
    align-items: center;
}

.department-bar__list {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 14px 0;
}

.department-bar__option {
    display: flex;
    align-items: center;
    text-align: center;
    padding-right: 15px;
    width: 146px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;  
    cursor: pointer;  
    color: #353535;
}

.department-bar__option:first-child {
    font-weight: 700;
    color: #FAA500;
}

